.checkout_section {
  /* margin: 1rem 0; */
  position: relative;
}

/* New styles for two-column layout */
.delivery_options_section .ant-card {
  margin-bottom: 1rem;
}

.choose_store .ant-card {
  margin-top: 1rem;
}

.save_address_list .ant-list-item {
  padding: 0.5rem 0;
}

.delivery_options_section .ant-form-item {
  margin-bottom: 0.5rem;
}

.checkout_section .ant-card-body {
  padding: 16px;
}

/* Order summary header styling */
.order_summary .ant-card-head-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.order_summary .ant-card-head-title img {
  border-radius: 8px;
  background: #f5f5f5;
  padding: 8px;
}

/* Quantity controls styling */
.add_cart_btn {
  display: flex;
  align-items: center;
  gap: 8px;
}

.add_cart_btn .ant-btn-group {
  display: flex;
}

.add_cart_btn .ant-btn-group .ant-btn {
  min-width: 32px;
  padding: 0 8px;
  font-size: 14px;
  border-radius: 4px;
}

.add_cart_btn .ant-btn-group .ant-btn:first-child,
.add_cart_btn .ant-btn-group .ant-btn:last-child {
  background-color: #52c41a;
  color: white;
  border-color: #52c41a;
}

.add_cart_btn .ant-btn-group .ant-btn:first-child:hover,
.add_cart_btn .ant-btn-group .ant-btn:last-child:hover {
  background-color: #389e0d;
  color: white;
  border-color: #389e0d;
}

.add_cart_btn .ant-btn-group .ant-btn:nth-child(2) {
  background-color: #f5f5f5;
  color: #000;
  border-color: #d9d9d9;
  cursor: default;
  pointer-events: none;
}

/* Professional checkout item layout */
.checkout_item_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 12px 0;
}

.item_info_section {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  flex: 1;
}

.item_details {
  flex: 1;
}

.item_title p {
  margin: 0;
  font-weight: 500;
  color: #333;
  font-size: 14px;
}

.item_actions_section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  min-width: 140px;
  padding: 8px;
}

.quantity_controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  width: 100%;
}

.quantity_price_info {
  display: flex;
  justify-content: space-between;
  width: 100%;
  gap: 12px;
}

.total_quantity,
.item_total_price {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  flex: 1;
  min-width: 60px;
  text-align: center;
}

.quantity_label,
.total_qty_label,
.price_label {
  font-size: 12px;
  color: #666;
  margin: 0;
  font-weight: 500;
}

.total_qty_value {
  font-size: 14px;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.price_value {
  font-size: 15px;
  font-weight: 700;
  color: #52c41a;
  margin: 0;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.price_value {
  font-size: 16px;
  font-weight: 700;
  color: #52c41a;
  margin: 0;
}

/* Responsive design for mobile */
@media (max-width: 768px) {
  .checkout_item_wrapper {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    padding: 8px 0;
  }
  
  .item_info_section {
    width: 100%;
    gap: 8px;
  }
  
  .item_actions_section {
    width: 100%;
    min-width: auto;
    background: #f8f9fa;
    padding: 12px;
    border-radius: 8px;
    gap: 12px;
  }
  
  .quantity_controls {
    width: 100%;
    align-items: center;
  }
  
  .quantity_price_info {
    width: 100%;
    justify-content: space-around;
    gap: 20px;
  }
  
  .total_quantity,
  .item_total_price {
    text-align: center;
    min-width: 80px;
  }
  
  .add_cart_btn .ant-btn-group .ant-btn {
    min-width: 36px;
    padding: 0 10px;
  }
}

@media (max-width: 768px) {
  .checkout_section {
    padding: 0 8px;
  }
  
  .checkout_section .container .row {
    flex-direction: column;
    margin: 0;
  }
  
  .checkout_section .container .row .col-md-6 {
    padding: 0 8px;
    margin-bottom: 1rem;
  }
  
  .delivery_options_section {
    margin-bottom: 1rem;
  }
  
  .delivery_options_section .ant-card {
    margin-bottom: 0.8rem;
  }
  
  .show_checkout_item {
    margin-bottom: 1rem;
  }
  
  .order_summary {
    margin-bottom: 1rem;
  }
  
  .checkout_main {
    margin: 0 !important;
    border-radius: 8px !important;
  }
  
  .checkout_main .ant-card-head {
    padding: 12px 16px;
  }
  
  .checkout_main .ant-card-body {
    padding: 16px;
  }
  
  .pay_grand {
    font-size: 14px;
  }
  
  .pay_grand p {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
  }
  
  /* Mobile specific quantity controls */
  .add_cart_btn {
    justify-content: center;
  }
  
  .quantity_label,
  .total_qty_label,
  .price_label {
    font-size: 11px;
    font-weight: 600;
  }
  
  .total_qty_value,
  .price_value {
    font-size: 14px;
    font-weight: 700;
  }
  
  /* Mobile button improvements */
  .pay_icons .ant-btn {
    width: 100%;
    height: 48px;
    font-size: 16px;
    font-weight: 600;
  }
  
  /* Mobile form improvements */
  .ant-input {
    height: 40px;
  }
  
  .ant-select-selector {
    height: 40px !important;
  }
  
  /* Mobile card improvements */
  .ant-card-small .ant-card-head {
    padding: 8px 16px;
  }
  
  .ant-card-small .ant-card-body {
    padding: 12px 16px;
  }
}

