.footer{
    margin-top: 1rem;
}
.footer_section{
    background-color: var(--secondry);
padding: 1rem;
}
.footer_icons{
    margin-top: -.2rem;
}

.footer h1, .footer p{
    color: gray;
}
.footer_data{
    margin-top: 1rem;
}
.footer_title p:first-child{
    font-weight: 600;
    margin-bottom: 1rem;
}
.Social_footer_title p:first-child{
    font-weight: 600;
    margin-bottom: 1rem;   
}
.Social_footer_title li{
    list-style: none;
}
.social_icons{
    margin-top: 1rem;
}
.call,.email{
    display: flex;
    align-items: center;
    margin-top: 1rem;
}
.call_data p:first-child{
    margin-bottom:  0;
}

.foot_icon{
    font-size: 1.5rem;
    margin-right: .5rem;
}
.call_data p:first-child{
    color: gray;
    font-size: 1rem;
}
/* .footer_title  li{
   list-style: none;
} */
.footer_title li a{
    color: black ;
    text-decoration: none;
}
.footer_title li a:hover{
    color: var(--primary);
    margin-left: .5rem ;
    transition: all 1s ease 0s 1 normal forwards ;
}
@keyframes myAnim {
	0% {
		opacity: 0;
		transform: translateX(-250px);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}
.social_iocns{
    width: 100%;
    margin-top: 1rem;
    display: flex;
    justify-content: space-evenly;
  
}
.social_icon{
    margin-top: -.2rem;
    font-size: 1.2rem;
    /* color: var(--primary); */
}

.social_icons .ant-btn-default:hover{
    background-color: var(--primary) !important;
    color: white  !important;
    border-style: none !important;

}

.copy_right{
    display: flex;
    justify-content: space-between;
}
.store_icon{
    font-size: 1.2rem;
    margin-right: .5rem;
}
.call_data a{
    cursor: pointer;
}
.call_data a:hover{
    color: rgb(48, 60, 229);
}