 .banenr_segment_title{
    display: flex;
    justify-content: space-between;
    align-items: center;
 }
.dual_segment_img img {
    width: 100%;
    object-fit: cover;
    aspect-ratio: 4/2;
    border-radius: 12px;
    cursor: pointer;
}
.segment_img img {
    width: 100%;

    aspect-ratio: 4/5;
    border-radius: 12px;
    cursor: pointer;
    object-fit: cover;
}
.banenr_segment img{
    object-fit: cover;
    border-radius: 8px;
    width: 100%;
    aspect-ratio: 8/2.5;
}
.banenr_segment{
    margin-bottom: 1.5rem;
}
.loading .ant-card-body{
     text-align: center;
     padding: .5rem;
}