.ot_field {
    width: 100%;
    text-align: center;
}

.ot_field .ant-input {
    width: 100%;
    border-color: var(--primary);
    text-align: center;
}

/* Hide spin buttons in Chrome, Safari, Edge, and Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Hide spin buttons in Firefox */

.change_number{
    font-size: .8rem;
    color: blue;
    cursor: pointer;
}
.not_recived_otp {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.not_recived_otp p {
    margin-bottom: 0;
}

.verify_continue {
    text-align: center;
    margin-top: 1rem;

}

.verify_continue button {
    background-color: var(--primary);
    color: white;
    border-color: var(--primary);
    padding: 0 3rem !important;
}

.log_continue {
    margin-top: .5rem;
    text-align: center;
}

.log_continue button {
    background-color: var(--primary);
    color: white;
    border-color: var(--primary);
    padding: 0 3rem !important;
}

.log_continue button:hover {
    color: white !important;
    border-color: var(--primary) !important;

}

.shop_lists .ant-card {
    margin-bottom: .5rem;
}

.log_page p {
    font-size: 1.2rem;
    font-weight: 600;
    cursor: pointer;
}

.log_page p:hover {
    color: var(--primary);
}

.login_title {
    margin-bottom: 1rem;
    text-align: center;
}

.log_head {
    text-align: center;
}

.log_head img {
    width: 50%;
}

.log_term {
    display: flex;
}

.log_term p,
.log_term a {
    font-size: .8rem;
}

.log_form {
    padding: 0 3rem;
}

.otp_vefiyed {
    padding: 0 4.5rem;

}

.signup_user {
    text-align: center;
}
.signup_user{
    display: flex;
    justify-content: center;
}
.sign_up_btn{
    margin-left: .3rem;
    color: var(--primary);
    cursor: pointer;
    font-weight: 500;
}
.sign_up_btn:hover{
    text-decoration: underline;
}

.signup_user p {
    color: gray;
}

.log_continue button {
    padding: 0 3rem;
}

.signup_confirmation img {
    width: 70%;
}

.signup_confirmation {
    text-align: center;
}

.signup_confirmation span {
    color: gray;
}

.show_verified {
    text-align: center;
}

.show_verified img {
    width: 70%;
}