.cat_flot {
  width: 100%;
}
.show_all_brand{
  /* display: flex !important; */
  width: 100%;
}

.cat_flot_img {
  width: 1.5rem;
}
.search_image{
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.brand_title{
  text-align: center;

}

.brand_title p::first-letter{
  text-transform: capitalize;
}
.trending-brand {
  display: flex !important;
  gap: 10px;
  align-items: center;
  border-radius: 50% !important;
}
.brand_title p{
  font-size: 1rem;
}
.brand img {
  width: 90%;
  aspect-ratio: 4/4;
  object-fit: contain;
  background-blend-mode: color-burn !important;
  cursor: pointer;
  -webkit-box-shadow: .5px .5px 8px -5.2px #000000;
  box-shadow: .5px .5px 8px -5.2px #000000;
  border-radius: 100%;
  padding: .5rem;
  
}
.seach_data_show{
  height: 60vh;
  overflow-y: scroll;
}
.brand_card .ant-card {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10rem;
  width: 10rem;
  background-color: var(--secondry);
  margin: 0.5rem;
}

.title_shop img {
  width: 15%;
  height: auto;
  margin-right: 0.5rem;
}

.title_shop {
  display: flex;
  align-items: center;
}

.title_shop p {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0;
}



.shop_image {
  width: 100%;
  position: relative;
  /* margin: .5rem; */
}

.shop_image 
.disabled-card {

  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.5;

}
.shop_slider{
  margin: 1rem;
}

.shop_image img {
  background-color: #eee;
  position: relative;
  width: 95%;
  aspect-ratio: 3/2;
  border-radius: 12px;
  cursor: pointer;
}

.recipes_titles {
  position: absolute;
  bottom: 0;
  /* margin: .5rem; */
  width: 95%;


  /* Fallback color */
  background: rgba(0, 0, 0, 0.5);
  /* Black background with opacity */
  color: #ffffff;
  padding: 20px 10px;
  border-radius: 0 0 10px 10px;
}

.shop_title {
  display: flex;
  justify-content: space-between;
}

.shop_title p {
  font-size: 1.2rem;
  font-weight: 600;
}

.shop_title button {
  font-size: 1.2rem;
  font-weight: 600;
}

.shop_loc {
  margin-top: 1rem;
}

.shop_loc p {
  font-weight: 600;
  font-size: 1rem;
}

.shop_dis {
  display: flex;
}

.shop_dis p {
  margin-right: 1rem;
}

.shop_lo_iocn {
  margin: 0 0.5rem 0.2rem 0;
  font-size: 1rem;
}

.shop_lists {
  margin-top: 1rem;
}

#shop_details {
  background-color: var(--primary);
  color: white;
  border-color: var(--primary);
}

.cat_tab_img img {
  width: 100%;
  padding: 0.5rem;
}

.cat_tab_img {
  display: flex;
  align-items: center;
  background-color: var(--secondry);
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
}

.cat_tabs .ant-card {
  background-color: var(--secondry);
}

.card_img {
  /* background-color: var(--secondry); */
  border-radius: 12px;
  text-align: center;
}

.default_search_pro button {
  text-transform: capitalize;
  margin-bottom: 1rem;
  width: 100%;
}

.default_search {
  margin: 1rem 0;
}

.search_his_icon {
  font-size: 1.2rem;
}

.pay_opts {
  margin-top: 1rem;
}

.pay_opts p {
  width: 100%;
}

.new_offer_Card {
  height: 95vh;
  margin-bottom: 1rem !important;
}

.new_offer_item img {
  width: 100%;
  height: 45vh;
  margin-top: 1rem;
  border-radius: 10px;
}

.search_results {
  padding: 0 3rem;
}

/* //discount css */

.card {

  border-radius: 10px;
  background: var(--primary);
  border: none;
  color: #fff;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
}

.show_all_coupon {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.coupon_image img {
  width: 4rem;
}

.show_all_coupon .ant-card-body {
  padding: .5rem !important;
}


.card h1 {
  font-size: 48px;
  margin-bottom: 0px;
}

.card span {
  font-size: 28px;
}

.bg_discount_img img {
  width: 80%;
}

.show_all_coupon .ant-card-body {
  display: flex;
  /* flex-direction: column; */
  cursor: pointer;
}

.coupon_content {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.bg_discount_img {
  /* width: 20%; */
  position: absolute;
  opacity: .1;
  left: 20%;
  top: 10%;
}

.apply_discount_img img {
  width: 60%;
}

.apply_discount_img {

  position: absolute;
  opacity: 1;
  left: 10%;
  top: 0;
}

.selected_card {
  border-color: var(--primary);
}

.offer_continue {
  text-align: center;
}

.offer_continue button {
  padding: 0 3rem !important;
  background-color: var(--primary);
  color: white;
  border-style: none;
}