.ribbon1 {
    position: absolute;
    top: -6.1px;
    right: 25%;
}

.ribbon1:after {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border-left: 30px solid transparent;
    border-right: 35px solid transparent;
    border-top: 10px solid #836FFF;
}

.variant_lists p {
    font-weight: 600;
}

.ribbon1 span {
    position: relative;
    display: block;
    text-align: center;
    background: #836FFF;
    font-size: 14px;
    line-height: 1;
    color: white;
    padding-top: 4px;
    border-top-right-radius: 8px;
    width: 4rem;
}

.ribbon1 span:before,
.ribbon1 span:after {
    position: absolute;
    content: "";
}

.ribbon1 span:before {
    height: 6px;
    width: 6px;
    left: -6px;
    top: 0;
    background: #836FFF;
}

.ribbon1 span:after {
    height: 6px;
    width: 8px;
    left: -8px;
    top: 0;
    border-radius: 8px 8px 0 0;
    background: #5f4ae7;
}

.ribbon1 span:before {
    height: 6px;
    width: 6px;
    left: -6px;
    top: 0;
    background: #836FFF;
}

.ribbon1 span:after {
    height: 6px;
    width: 8px;
    left: -8px;
    top: 0;
    border-radius: 8px 8px 0 0;
    background: #5f4ae7;
}