.terms h4 {
    position: relative;
    display: inline-block;
    padding-bottom: 5px;
    /* Adjust based on your design */
}

.faq_img img {
    width: 100%;
}

.faq_item {
    margin-top: 2rem;
}

.term_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.term_title {
    margin-bottom: 2rem;
}

.help_support a {
   text-decoration: none;
   color: rgb(20, 20, 20);

}

.help_support {
    display: flex;
    align-items: center;



}

.help_support h5 {
    margin-bottom: 0;
    margin: 0 .5rem;
}



.terms h4::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4px;
    /* Adjust based on your design */
    background: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
}

.terms {
    padding: 2rem 0;
}