.tabs-scroll-wrapper .ant-tabs-nav {
  overflow: hidden;
}

.tabs-scroll-wrapper .ant-tabs-nav-wrap {
  overflow-x: auto !important;
  overflow-y: hidden;
  white-space: nowrap;
  scrollbar-width: none;
  /* scrollbar-width: thin;
  scrollbar-color: #999 transparent; */
}

/* Hide scrollbar by default */
.tabs-scroll-wrapper .ant-tabs-nav-wrap::-webkit-scrollbar {
  height: 6px;
  display: none;
  background: transparent;
}

/* Show scrollbar on hover */
.tabs-scroll-wrapper:hover .ant-tabs-nav-wrap::-webkit-scrollbar {
  display: block;
}

.tabs-scroll-wrapper .ant-tabs-nav-wrap::-webkit-scrollbar-thumb {
  background: #bbb;
  border-radius: 6px;
}




