/* Container for marquee effect */
.ant-input::placeholder {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;

  animation: myAnim 2s ease 0s 1 normal forwards;
}

@keyframes myAnim {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
